            <div>
              <div class="header-custom-fontsize">Status</div>
              <DxTextArea :height="34" :read-only="true" :value="data.data.status" />
            </div>
            <div>
              <div class="header-custom-fontsize">Inspection Procedure:</div>
              <DxTextArea :height="34" :read-only="true" :value="data.data.insp_procedure" />
