            </div>
          </div>
        </template>
        <template #detail-template-responsive="{ data }">
          <div class="container">
            <div class="row">
              <div class="col-lg-4 col-md-6 mb-2">
                <div class="header-custom-fontsize">Inspection Method:</div>
                <DxTextArea :height="34" :read-only="true" :value="data.data.insp_method" />
              </div>
              <div class="col-lg-4 col-md-6 mb-2">
                <div class="header-custom-fontsize">Status</div>
