              </div>
              <div class="col-lg-4 col-md-6 mb-2">
                <div class="header-custom-fontsize">Inspection Procedure:</div>
                <DxTextArea :height="34" :read-only="true" :value="data.data.insp_procedure" />
              </div>
              <div class="col-lg-4 col-md-6 mb-2">
                <div class="header-custom-fontsize">Inspection Equipment</div>
                <DxTextArea :height="34" :read-only="true" :value="data.data.insp_equipment" />
              </div>
              <div class="col-lg-4 col-md-6 mb-2">
                <div class="header-custom-fontsize">Calibration Tube No.</div>
                <DxTextArea :height="34" :read-only="true" :value="data.data.calibration_tube_no" />
              </div>
              <div class="col-lg-4 col-md-6 mb-2">
                <div class="header-custom-fontsize">Calibration Standard</div>
                <DxTextArea :height="34" :read-only="true" :value="data.data.calibration_standard" />
              </div>
              <div class="col-lg-4 col-md-6 mb-2">
                <div class="header-custom-fontsize">Outside Diameter Probe (mm)</div>
                <DxTextArea
                  :height="34"
