              </div>
              <div class="col-lg-4 col-md-6 mb-2">
                <div class="header-custom-fontsize">Tested Type</div>
                <DxTextArea :height="34" :read-only="true" :value="data.data.tested_type" />
              </div>
              <div class="col-lg-4 col-md-6 mb-2">
                <div class="header-custom-fontsize">Test side</div>
                <DxTextArea :height="34" :read-only="true" :value="data.data.test_side" />
              </div>
              <div class="col-lg-4 col-md-6 mb-2">
                <div class="header-custom-fontsize">Frequency</div>
                <DxTextArea :height="34" :read-only="true" :value="data.data.freq" />
              </div>
              <div class="col-lg-4 col-md-6 mb-2">
                <div class="header-custom-fontsize">Sensitivity</div>
                <DxTextArea :height="34" :read-only="true" :value="data.data.sensitivity" />
