              <!-- <hr /> -->
              <div class="col-12 mb-2">
                <div class="header-custom-fontsize">Finding</div>
                <DxTextArea :height="60" :read-only="true" :value="data.data.finding" />
              </div>
              <div class="col-12 mb-2">
                <div class="header-custom-fontsize">Recommendation</div>
                <DxTextArea :height="60" :read-only="true" :value="data.data.recommendation" />
              </div>
