        <template #tube-img="{ data }">
          <div style="position: relative">
            <!-- <div class="header-custom-fontsize" style="padding-bottom:3px">
              <span>Tube</span>
            </div>-->
            <a :href="baseURL + data.value" download="dwg" target="_blank">
              <img :src="baseURL + data.value" width="300" height="200" />
              <br />
            </a>
          </div>
        </template>
        <template #tube-icon="{data}">
          <div class="d-md-none" v-if="data.data.file_path.length>0">
            <i
              class="fa-regular fa-file"
              style="display:flex; font-size:36px; justify-content:center"
            ></i>
            <div style="display:flex; justify-content:center; margin-top:5px">
              <label style="width:100%">{{ data.data.file_name }}</label>
            </div>
          </div>
        </template>
        <template #tube-file-editor>
          <div>
            <!-- <i class="fa-regular fa-file" style="display:flex; font-size:36px; padding:10px"></i> -->
            <DxFileUploader
              select-button-text="Upload file"
              label-text
              accept
              upload-mode="useForm"
              style="padding-top:10px"
              @value-changed="ON_REPAIR_CHANGE"
            />
          </div>
        </template>
        <template #tube-img-editor="{ data }">
          <div>
            <img
              :src="baseURL + data.value"
              width="400"
              height="300"
              v-if="imgTube != '' && isInitEdit == 0"
            />
            <img :src="imgTube" width="400" height="300" v-if="imgTube != '' && isInitEdit == 1" />
            <img
              src="http://tmt-solution.com/public/image-empty.png"
              width="400"
              height="300"
              v-if="imgTube == ''"
