</template> 

<script>
//API
import axios from "/axios.js";
import moment from "moment";

//Components
import "devextreme/dist/css/dx.light.css";
import "@fortawesome/fontawesome-free/css/all.css";
// import innerPageName from "@/components/app-structures/app-inner-pagename.vue";
import InspectionRecordPanel from "@/views/Applications/Vessel/Pages/inspection-record-panel.vue";
import SelectInspRecord from "@/components/select-insp-record.vue";
import DxTextArea from "devextreme-vue/text-area";

//DataGrid
import {
  DxDataGrid,
  DxSearchPanel,
  DxPaging,
  DxPager,
  DxScrolling,
  DxColumn,
  DxExport,
  DxEditing,
  //DxPopup,
  DxForm,
  DxLookup
  //DxRequiredRule
} from "devextreme-vue/data-grid";

//List
// import { DxList } from "devextreme-vue/list";

//FileUpload
import { DxFileUploader } from "devextreme-vue/file-uploader";
//import { DxButton } from 'devextreme-vue/button';
import { DxItem } from "devextreme-vue/form";

const fileUploaderRef = "fu";
const imgRef = "img";

export default {
  name: "TubeInspection",
  components: {
    //VueTabsChrome,
    // DxList,
    DxDataGrid,
    DxSearchPanel,
    DxPaging,
    DxPager,
    DxScrolling,
    DxColumn,
    DxExport,
    DxEditing,
    DxFileUploader,
    DxForm,
    DxItem,
    //DxPopup,
    //DxButton,
    // innerPageName,
    InspectionRecordPanel,
    SelectInspRecord,
    DxTextArea,
    //DxRequiredRule
    DxLookup
  },
  created() {
    this.$store.commit("UPDATE_CURRENT_PAGENAME", {
      subpageName: "Tube Inspection"
    });
  },
  data() {
    return {
      tubeList: [],
      isLoading: false,
      fileUploaderRef,
      statusList: [
        {
          code: "status1"
        },
        {
          code: "status2"
        }
      ],
      imgRef,
      imgTube: "",
      file: [],
      isInitEdit: 0,
      id_inspection_record: 0,
      dataGridAttributes: {
        class: "data-grid-style-custom"
      },
      pagePanelHiding: false,
      current_view: {},
      is_changed_tube: 0,
      dataTubeTemp: "",
      findingInputOptions: { placeholder: "Enter finding ..." },
      recInputOptions: { placeholder: "Enter recommendation ..." }
    };
  },
  computed: {
    baseURL() {
      var mode = this.$store.state.mode;
      if (mode == "dev") return this.$store.state.modeURL.dev;
      else if (mode == "prod") return this.$store.state.modeURL.prod;
      else return console.log("develpment mode set up incorrect.");
    }
  },
  methods: {
    VIEW_ITEM(item) {
      this.current_view = {};
      this.tubeList = [];
      this.id_inspection_record = item.id_inspection_record;
      this.current_view = item;
      console.log(this.id_inspection_record);
      axios({
        method: "get",
        url:
          "/TubeInspectionReport/get-tube-by-insp-id/" +
          this.id_inspection_record,
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        }
      })
        .then(res => {
          if (res.status == 200 && res.data) {
            this.tubeList = res.data;
          }
        })
        .catch(error => {
          console.log(error);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    CREATE_TUBE(e) {
      const user = JSON.parse(localStorage.getItem("user"));
      var formData = new FormData();
      formData.append("id_vessel", this.$route.params.id_tag);
      formData.append("id_inspection_record", this.id_inspection_record);
      formData.append("insp_method", e.data.insp_method ?? "");
      formData.append("status", e.data.status ?? "");
      formData.append("insp_procedure", e.data.insp_procedure ?? "");
      formData.append("insp_equipment", e.data.insp_equipment ?? "");
      formData.append("calibration_tube_no", e.data.calibration_tube_no ?? "");
      formData.append(
        "calibration_standard",
        e.data.calibration_standard ?? ""
      );
      formData.append("test_side", e.data.test_side ?? "");
      formData.append("OD_probe_mm", e.data.OD_probe_mm ?? 0.0);
      formData.append("transducer_freq", e.data.transducer_freq ?? "");
      formData.append("freq", e.data.freq ?? "");
      formData.append("sensitivity", e.data.sensitivity ?? "");
      formData.append("tested_type", e.data.tested_type ?? "");
      formData.append("finding", e.data.recommendation ?? "");
      formData.append("recommendation", e.data.recommendation ?? "");
      formData.append("file", this.file);
      formData.append("created_by", user.id);
      formData.append("updated_by", user.id);

      axios({
        method: "post",
        url: "TubeInspectionReport",
        headers: {
          "Content-Type": "multipart/form-data",
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        },
        data: formData
      })
        .then(res => {
          console.log(res);
          if (res.status == 201 && res.data) {
            console.log(res.data);

            this.VIEW_ITEM(this.current_view);
          }
        })
        .catch(error => {
          console.log(error);
        })
        .finally(() => {
          this.isLoading = false;
          this.is_changed_tube = 0;
        });
    },
    UPDATE_TUBE(e) {
      console.log(e);
      //const user = JSON.parse(localStorage.getItem("user"));
      var formData = new FormData();
      formData.append("id", e.key);
      formData.append("id_vessel", this.$route.params.id_tag);
      formData.append("id_inspection_record", this.id_inspection_record);
      formData.append("insp_method", e.data.insp_method ?? "");
      formData.append("status", e.data.status ?? "");
      formData.append("insp_procedure", e.data.insp_procedure ?? "");
      formData.append("insp_equipment", e.data.insp_equipment ?? "");
      formData.append("calibration_tube_no", e.data.calibration_tube_no ?? "");
      formData.append(
        "calibration_standard",
        e.data.calibration_standard ?? ""
      );
      formData.append("test_side", e.data.test_side ?? "");
      formData.append("OD_probe_mm", e.data.OD_probe_mm ?? 0.0);
      formData.append("transducer_freq", e.data.transducer_freq ?? "");
      formData.append("freq", e.data.freq ?? "");
      formData.append("sensitivity", e.data.sensitivity ?? "");
      formData.append("tested_type", e.data.tested_type ?? "");
      formData.append("finding", e.data.recommendation ?? "");
      formData.append("recommendation", e.data.recommendation ?? "");
      formData.append("file_path", e.data.file_path);
      formData.append("file", this.file ?? "");
      axios({
        method: "put",
        url: "/TubeInspectionReport/" + e.key,
        headers: {
          "Content-Type": "multipart/form-data",
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        },
        data: formData
      })
        .then(res => {
          console.log(res);
          if (res.status == 204) {
            console.log(res.data);
            var item = [];
            item.id_inspection_record = this.id_inspection_record;
            this.VIEW_ITEM(item);
          }
        })
        .catch(error => {
          console.log(error);
        })
        .finally(() => {
          this.isLoading = false;
          this.is_changed_dwg = 0;
        });
    },
    DELETE_TUBE(e) {
      console.log(e);
      axios({
        method: "delete",
        url: "/TubeInspectionReport/" + e.key,
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        }
      })
        .then(res => {
          console.log(res);
          if (res.status == 200 && res.data) {
            console.log(res.data);
            var item = [];
            item.id_inspection_record = this.id_inspection_record;
            this.VIEW_ITEM(item);
          }
        })
        .catch(error => {
          console.log(error);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    ON_REPAIR_CHANGE(e) {
      console.log(e);
      this.isInitEdit = 1;
      let reader = new FileReader();
      reader.readAsDataURL(e.value[0]);
      reader.onload = () => {
        this.imgTube = reader.result;
      };
      this.file = e.value[0];
      this.is_changed_tube = 1;
    },
    EDIT_START(e) {
      console.log(e);
      this.imgTube = e.data.path_repair;
      this.file = [];
      this.isInitEdit = 0;
      this.file_path = e.data.file_path;
      this.dataTubeTemp = e;
    },
    INIT_NEW_ROW() {
      this.imgTube = "";
      this.file = [];
      this.isInitEdit = 1;
    },
    SAVE(e) {
      console.log("save:");
      console.log(e);
      if (e.changes.length == 0) {
        this.UPDATE_TUBE(this.dataTubeTemp);
      }
    },
    IS_VISIBLE_ADD() {
      if (this.id_inspection_record == 0) {
        return false;
      } else {
        return true;
      }
    },
    SHOW_HIDE_PANEL() {
      this.pagePanelHiding = !this.pagePanelHiding;
    },
    DATE_FORMAT(d) {
      return moment(d).format("LL");
    }
  },
  watch: {}
};
</script>

